import { Component, Input } from '@angular/core';
import { CommonFacade } from '@core/facades/common.facade';
import { ViewportService } from '@core/services/viewport.service';
import { IconService } from '@core/services/icon.service';

@Component({
    selector: 'findqo-my-ad-stamp',
    templateUrl: './my-ad-stamp.component.html',
    styleUrls: ['./my-ad-stamp.component.scss'],
    standalone: false
})
export class MyAdStampComponent {
	constructor(
		public viewport: ViewportService,
		public icon: IconService,
		private commonFacade: CommonFacade
	) {}

	@Input() set adStatus(status) {
		this.stampTxt = status;
		this.adStatusConfig = this.statusMappings[status];
	}

	stampTxt: string;
	adStatusConfig;

	statusMappings = {
		live: {
			bgColor: '#E3FFE6',
			txtColor: '#2EB844',
			hasIcon: false,
			hasBorder: true,
		},
		draft: {
			bgColor: '#F3F4F6',
			txtColor: '#6C7284',
			hasIcon: false,
			hasBorder: true,
		},
		'payment-pending': {
			bgColor: '#E7F1FF',
			txtColor: '#0D6EFD',
			hasIcon: false,
			hasBorder: true,
		},
		'payment-failed': {
			bgColor: '#FFE9EA',
			txtColor: '#ED4258',
			hasIcon: false,
			hasBorder: true,
		},
		'under-review': {
			bgColor: '#E7F1FF',
			txtColor: '#0D6EFD',
			hasIcon: false,
			iconType: 'cancel',
			iconClass: 'fg-info-3',
			hasBorder: true,
		},
		'edit-under-review': {
			bgColor: '#E7F1FF',
			txtColor: '#0D6EFD',
			hasIcon: false,
			iconType: 'cancel',
			iconClass: 'fg-info-3',
			hasBorder: true,
		},
		rejected: {
			bgColor: '#FFE9EA',
			txtColor: '#ED4258',
			hasIcon: false,
			iconType: 'help',
			iconClass: 'fg-error-2',
			hasBorder: true,
		},
		expired: {
			bgColor: '#FFEDD5',
			txtColor: '#EA580C',
			hasIcon: false,
			hasBorder: true,
		},
		sold: {
			bgColor: '#FFEDD5',
			txtColor: '#EA580C',
			hasIcon: false,
			hasBorder: true,
		},
		completed: {
			bgColor: '#E3FFE6',
			txtColor: '#2EB844',
			hasIcon: false,
			hasBorder: false,
			isRoundedPill: true,
		},
		pending: {
			bgColor: '#F5F5F5',
			txtColor: '#6C7284',
			hasIcon: false,
			hasBorder: false,
			isRoundedPill: true,
		},
		unpublished: {
			bgColor: '#FFEDD5',
			txtColor: '#EA580C',
			hasIcon: false,
			hasBorder: true,
		},
		'property-occupied': {
			bgColor: '#FFEDD5',
			txtColor: '#EA580C',
			hasIcon: false,
			hasBorder: true,
		},
		'sale-agreed': {
			bgColor: '#FFEDD5',
			txtColor: '#EA580C',
			hasIcon: false,
			hasBorder: true,
		},
	};

	removeHyphens(inputString: string): string {
		return inputString.replace(/-/g, ' ');
	}
}
