// browser utility.service.ts
import { Injectable } from '@angular/core';
import { DevicePlateformService } from './device-plateform.service';
import { DevicePlateform } from '@core/enums/device-plateform.enum';

@Injectable({
	providedIn: 'root',
})
export class BrowserUtility {
	constructor(private readonly devicePlatform: DevicePlateformService) {}
	isWebShareSupported(): boolean {
		// Browser's are now also providing native share for Desktops, so we are adding check for devices as well
		const PLATFORM = this.devicePlatform.getPlatform();
		return 'share' in navigator && (PLATFORM === DevicePlateform.ANDROID || PLATFORM === DevicePlateform.IOS || PLATFORM === DevicePlateform.HUAWEI);
	}
}
